/* style for mobile */
body {
  background: rgb(227, 223, 218);
  color: rgb(50, 50, 50);
 }
 a {
  text-decoration: none;
  cursor: pointer;
  color: rgb(0, 110, 200);
 }
 h1 {
  margin-left: .5em;
 }
 h3 {
  margin-top: 2em;
  margin-bottom: .1em;
  margin-left: 1em;
 }
 /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
 .navContainer {
  z-index: 1;
  display: grid;
  grid-template-columns: 85% auto;
  grid-template-rows: auto;
  position: fixed;
  width: 100%;
  padding: .5em;
  background: rgb(68, 69, 69);
  color: rgb(0, 181, 245);
  font-size: 1.5em;
 }
 .header {
  grid-column: 1;
 }
 .iconContainer {
  max-width: 100%;
  grid-column: 2;
  display: flex;
  justify-content: right;
 }
 #menuIcon {
  position: relative;
  grid-column: 2;
  top: .1em;
  right: 1.2em;
 }
 .menuContainer {
  display: flex;
  justify-content: right;
  grid-column: 1 / span 2;
  top: -2.5em;
 }
 .menuList{
  list-style: none;
 }
 .navLink {
  color: rgb(0, 181, 245);
  padding-right: 1.5em;
 }
 .contentLink {
  color: rgb(0, 110, 200);
 }
 .menuList > li {
  padding: .5em;
 }
 /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
 .missionContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 2em;
 }
 #groupPhoto {
  background: #6114A4;
  background: -moz-linear-gradient(45deg, #6114A4 0%, #2388CB 59%, #00FFFFFF 100%);
  background: -webkit-linear-gradient(45deg, #6114A4 0%, #2388CB 59%, #00FFFFFF 100%);
  background: linear-gradient(45deg, #6114A4 0%, #2388CB 59%, #00FFFFFF 100%);
  max-width: 100%;
  margin-bottom: -1em;
 }
 .content {
  padding-right: 1.5em;
  padding-left: 1.5em;
  margin-bottom: 0em;
}
.doLists > li {
  padding-right: 2em;
  padding-bottom: .5em;
}
 /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
.calendarContainer {
  top: 5em;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.circleHeader {
  align-items: revert;
}
.calendar {
  max-width: 90vmin;
  height: 90vmin;
  margin-top: 2em;
  margin-bottom: 3em;
}

 /* styles for desktop when the browser is 600px or above */
 @media (min-width: 900px) {
   .navContainer {
     grid-template-columns: 70% auto;
    }
    .menuList {
     grid-column: 2;
     font-size: .75em;
     display: flex;
     justify-content: right;
     /* left: -3em; */
    }
    .navLink {
      padding-right: 2em;
    }
    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
    .missionContainer {
      display: grid;
      grid-template-columns: 50% 50%;
      /* grid-template-rows: 35% 55% 10%; */
      grid-template-rows: auto;
    } 
    #groupPhoto {
      max-width: 45em;
      margin-bottom: -1em;
      grid-column: 1;
      grid-row: 1;
    }
    .mission1 {
      grid-column: 1;
      grid-row: 2;
    }
    .mission2 {
      grid-column: 2;
      grid-row: 2;
    }
    .mission3 {
      grid-column: 2;
      grid-row: 3;
    }
 }
 
